/*--------------------------------------------
  bg side
--------------------------------------------*/
.c-deco_bgSide {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: calc(50% + 60rem);
    height: 60rem;
    background-color: $beige;
  }

  > * {
    position: relative;
  }

  &.-reverse {
    &::before {
      left: 0;
      right: auto;
    }
  }

  @include mq(md) {
    &::before {
      width: 95%;
    }
  }
  @include mq(sm) {
    &::before {
      height: 30rem;
    }
  }
  @include mq(xs) {
    &::before {
      height: 24rem;
    }
  }
}
