/*--------------------------------------------
  card
--------------------------------------------*/
.p-card {
  display: block;
  @include hover_img(1.05);

  &__imgWrap {
    position: relative;
    border: 1px solid $bd;

    .c-label {
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
    }
    .c-ofi {
      padding-top: 100%;
    }
  }

  &__date {
    display: block;
    margin-top: 1.4em;
    font-size: 1.4rem;
    line-height: 1.3;
  }
  &__title {
    margin-top: 0.8em;
    font-weight: 500;
    line-height: 1.5;
  }

  &:hover {
    color: $sub;
  }

  @include mq(sm) {
    &__date {
      font-size: 1.2rem;
    }
  }
}

/*--------------------------------------------
  border box
--------------------------------------------*/
.p-card_bdBox {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 20rem;
  padding: 1.5em;
  border: 1px solid $main;
  text-align: center;

  &__text {
    font-weight: 500;
    font-size: 3rem;
    color: $main;
    line-height: 1.3;
  }

  @include mq(sm) {
    min-height: 16rem;
    padding: 1em;

    &__text {
      font-size: 2.2rem;
    }
  }
  @include mq(xs) {
    min-height: auto;
    padding: 1.5em 0.8em;

    &__text {
      font-size: 2rem;
    }
  }
}

/*--------------------------------------------
  thumbnail
--------------------------------------------*/
.p-card_thumb {
  $amp: &;
  display: block;
  @include hover_img;

  &__pic {
    padding-top: 140%;
    border: 1px solid $bd;
  }
  &__text {
    margin-top: 0.8em;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.3;
  }

  &:hover {
    color: $main;
  }

  &.-products {
    #{$amp}__pic {
      padding-top: 71%;
      border: none;
    }
    #{$amp}__text {
      text-align: center;
    }
  }

  @include mq(sm) {
    &__text {
      margin-top: 0.6em;
      font-size: 2rem;
    }
  }
}

/*--------------------------------------------
  sdgs
--------------------------------------------*/
.p-card_sdgs {
  display: block;

  &__logoWrap {
    padding: 2em;
  }

  &:hover {
    opacity: $opacity;
  }

  @include mq(sm) {
    &__logoWrap {
      padding: 1.5em;
    }
  }
  @include mq(xs) {
    &__text {
      text-align: center;
    }
  }
}
