/*--------------------------------------------
  variable
--------------------------------------------*/
$manual_ff: "Yu Gothic Medium", "Yu Gothic", 'Hiragino Kaku Gothic ProN', 'Hiragino Kaku Gothic Pro', 'Hiragino Sans', Meiryo, Verdana, arial, sans-serif;;

$manual_body    : #fff;
$manual_font    : #222;
$manual_main    : #003974;
$manual_link    : #0086e5;
$manual_white   : #fff;
$manual_gray    : #eee;

$manual_gutter: 5vw;

$manual_opacity: .8;




/*--------------------------------------------
  BASE
--------------------------------------------*/
.l-page {
  &.-manual {
    position: relative;
    min-height: 100vh;
    padding-bottom: 1em;
    background: $manual_body;
    font-family: $manual_ff;
    color: $manual_font;

    .l-content {
      padding-top: 0;
    }

    .wp-editor {
      h2 {
        padding: .8em 1em;
        background-color: $manual_main;
        border-left: .5rem solid $manual_main;
        font-weight: 400;
        font-size: 2.4rem;
        color: $manual_white;
        line-height: 1.3;
        letter-spacing: .1rem;
      }
      h3 {
        padding-bottom: .4em;
        border-bottom: .5rem solid $manual_main;
        font-weight: 400;
        font-size: 2.4rem;
        line-height: 1.3;
        letter-spacing: .1rem;
      }
      h4 {
        position: relative;
        padding-left: 1.5em;
        font-weight: 400;
        font-size: 1.8rem;
        line-height: 1.3;
        letter-spacing: .1rem;

        &::before {
          content: '';
          display: block;
          position: absolute;
          top: .3em;
          left: 0;
          width: .6em;
          height: .6em;
          background-color: $manual_main;
        }
      }


      a {
        color: $manual_link;
      }


      ul:not([class]),
      ol:not([class]) {
        padding: 3em;
        padding-left: 4em;
        border: .4rem solid $manual_main;

        > li {
          padding-bottom: .8em;
          border-bottom: 1px solid $manual_main;

          &:not(:first-child) {
            margin-top: .8em;
          }

          > ul,
          > ol {
            margin-top: .8em;
          }
        }
      }
    }

    @include mq(sm) {
      h2 {
        border-width: .4rem;
        font-size: 2rem;
      }
      h3 {
        border-width: .4rem;
        font-size: 2rem;
      }
      h4 {
        padding-left: 1.3em;
        font-size: 1.6rem;

        &::before {
          top: .4em;
          width: .5em;
          height: .5em;
        }
      }

      ul:not([class]),
      ol:not([class]) {
        padding: 1.5em;
        padding-left: 2.5em;
        border-width: 2px;
      }
    }
  }
}




/*--------------------------------------------
  header
--------------------------------------------*/
.p-manual_header {
  padding: 1.2em 0;
  background-color: $manual_main;

  &__inr {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__heading {
    flex-grow: 1;
    display: flex;
    align-items: center;
    padding-right: 2em;

    > a {
      display: block;
      font-weight: 500;
      font-size: 2.4rem;
      color: $manual_white;
      line-height: 1.3;

      &:hover {
        opacity: $manual_opacity;
      }
    }
  }

  &__menu {
    display: flex;
  }
  &__adminBtn {
    display: block;
    padding: .6em 1.2em;
    border: 1px solid $manual_white;
    font-size: 1.4rem;
    color: $manual_white;
    line-height: 1.3;

    &:hover {
      background-color: $manual_white;
      color: $manual_main;
    }
  }
}




/*--------------------------------------------
  content
--------------------------------------------*/
.p-manual_kv {
  display: flex;
  align-items: center;
  min-height: 10rem;
  padding: 1em 0;
  background-color: $manual_gray;


  &__title {
    padding-left: .8em;
    border-left: .5rem solid $manual_main;
    font-size: 2.4rem;
    line-height: 1.5;
  }

  @include mq(sm) {
    &__title {
      font-size: 2rem;
    }
  }
}



.p-manual_mainConts {
  padding-top: 7em;
  padding-bottom: 7em;

  @include mq(sm) {
    padding-top: 4em;
    padding-bottom: 4em;
  }
}



.p-manual_post {
  $amp: &;
  margin-top: -1.5em;

  &__item {
    border-bottom: 1px solid $manual_main;
  }

  &__link {
    display: flex;
    flex-wrap: wrap;
    padding: 1.5em;
    padding-left: 3em;
    color: $manual_font;

    &:hover {
      background-color: $manual_gray;

      #{$amp}__title {
        text-decoration: underline;
      }
    }
  }
  &__infoWrap {
    position: relative;
    width: 20rem;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: .6em;
      left: -1.3em;
      width: .5em;
      height: .5em;
      border-radius: 50%;
      background-color: $manual_main;
    }
  }
  &__title {
    width: calc(100% - 20rem);
  }

  @include mq(sm) {
    &__link {
      padding: 1.3em;
      padding-left: 2em;
    }
    &__infoWrap {
      width: 17rem;

      &::before {
        top: .7em;
        left: -1.2em;
        width: 6px;
        height: 6px;
      }
    }
    &__title {
      width: calc(100% - 17rem);
    }
  }
  @include mq(xs) {
    &__infoWrap {
      width: 100%;

      &::before {
        top: .7em;
        width: 5px;
        height: 5px;
      }
    }
    &__title {
      width: 100%;
    }
  }
}


.p-manual_btnWrap {
  margin-top: 5em;
  text-align: center;

  @include mq(sm) {
    margin-top: 3em;
  }
}


.p-manual_btn {
  display: inline-block;
  width: 22rem;
  max-width: 100%;
  padding: 1em;
  background-color: $manual_gray;
  border: 1px solid $manual_main;
  text-align: center;
  font-size: 1.8rem;
  color: $manual_font;
  line-height: 1.3;
  letter-spacing: .1rem;

  &:hover {
    background-color: $manual_main;
    color: $manual_white;
  }

  @include mq(sm) {
    width: 18rem;
    padding: .8em;
    font-size: 1.6rem;
  }
}



/*--------------------------------------------
  footer
--------------------------------------------*/
.p-manual_footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-top: 1em solid $manual_main;
}

