/*--------------------------------------------
  link
--------------------------------------------*/
.c-link {
  $amp: &;
  display: flex;
  margin-left: -2em;

  &__item {
    padding-left: 2em;
  }

  &__anc {
    display: block;
    line-height: 1.3;

    &:hover {
      text-decoration: underline;
    }
  }

  &.-white {
    #{$amp}__anc {
      color: $white;
    }
  }
}

/*--------------------------------------------
  sns
--------------------------------------------*/
.c-link_sns {
  display: flex;
  align-items: center;

  &__anc {
    display: block;
    padding: 1em;

    &:hover {
      opacity: $opacity;
    }
  }
}

/*--------------------------------------------
  bar
--------------------------------------------*/
.c-link_bar {
  $amp: &;
  display: flex;
  align-items: center;
  margin: 0 -1em;
  overflow: hidden;

  &__item {
    padding: 0 1em;
    border-left: 1px solid $font;
  }
  &__anc {
    display: block;
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 1.5;

    &:hover {
      text-decoration: underline;
    }
  }

  &.-en {
    #{$amp}__anc {
      font-size: 1.6rem;
    }
  }

  &.-white {
    #{$amp}__item {
      border-color: $white;
    }
    #{$amp}__anc {
      color: $white;
    }
  }

  @include mq(sm) {
    &.-en {
      #{$amp}__anc {
        font-size: 1.8rem;
      }
    }
  }
}

/*--------------------------------------------
  border bottom
--------------------------------------------*/
.c-list_bdb {
  &__item {
    border-bottom: 1px solid $bd;

    > a {
      display: block;
      padding: 1em 0;
      line-height: 1.3;

      &:hover {
        color: $main;
      }
    }

    &.-active {
      border-bottom-color: $sub;

      > a {
        font-weight: 500;
        color: $sub;
        pointer-events: none;
      }
    }
  }
}
