.c-modal {
  opacity: 0;
  pointer-events: none;
  z-index: 105;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity .4s;

  &.is-act {
    opacity: 1;
    pointer-events: auto;
  }

  &__overlay {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba($black, .7);
  }
  &__close {
    display: block;
    z-index: 3;
    position: absolute;
    top: 2em;
    right: 2em;
    width: 3em;
    height: 3em;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    transform: rotate(45deg);

    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 100%;
      height: 1px;
      background-color: #fff;
      @include transition;
    }
    &::after {
      transform: rotate(90deg);
    }

    &:hover {
      &::before,
      &::after {
        opacity: .7;
      }
    }
  }

  &__item {
    opacity: 0;
    z-index: 2;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    width: 120rem;
    max-width: 92%;
    max-height: calc(100vh - 15em);
    min-height: 10em;
    overflow: auto;
    margin: 0 auto;
    background-color: #fff;
    transform: translateY(-50%);
    transition: .4s;
    pointer-events: none;

    &.is-open {
      opacity: 1;
      pointer-events: auto;
    }
  }


  @include mq(sm) {
    &__close {
      top: 3.5vw;
      right: 3.5vw;
      width: 2.5em;
      height: 2.5em;
    }

    &__item {
      max-height: calc(100vh - ( 3.5vw + 3em ) * 2);
    }
  }
}
