/*--------------------------------------------
  sup
--------------------------------------------*/
.c-ttl_sup {
  $amp: &;
  margin-bottom: 3em;
  text-align: center;

  &__sup {
    margin-bottom: 0.8em;
    font-weight: 500;
    font-size: 1.2rem;
    color: $main;
    line-height: 1.3;
  }

  &__title {
    font-weight: 500;
    font-size: 4rem;
    line-height: 1.3;

    &.-ls {
      letter-spacing: 1rem;
    }
  }

  .-en & {
    #{$amp}__sup {
      font-weight: 400;
      font-size: 1.6rem;
      line-height: 1.1;
    }

    #{$amp}__title {
      font-weight: 400;
      font-size: 4.6rem;
      line-height: 1.1;

      &.-lg {
        font-size: 7rem;
      }
    }
  }

  @include mq(sm) {
    margin-bottom: 2em;

    &__sup {
      margin-bottom: 0.6em;
      font-size: 1.4rem;
    }

    &__title {
      font-size: 3rem;

      &.-ls {
        letter-spacing: 0.5rem;
      }
    }

    .-en & {
      #{$amp}__title {
        font-size: 3.6rem;

        &.-lg {
          font-size: 4.2rem;
        }
      }
    }
  }
  @include mq(xs) {
    &__title {
      font-size: 2.4rem;

      &.-ls {
        letter-spacing: 0.2rem;
      }
    }

    .-en & {
      #{$amp}__title {
        font-size: 3rem;

        &.-lg {
          font-size: 3.6rem;
        }
      }
    }
  }
}

/*--------------------------------------------
  sup small
--------------------------------------------*/
.c-ttl_supSm {
  margin-bottom: 0.8em;

  &__sup {
    margin-bottom: 0.4em;
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 1.1;
  }
  &__main {
    font-weight: 400;
    font-size: 2.4rem;
    line-height: 1.3;
  }
  &__mainSm {
    margin-left: 0.8em;
    font-size: 1.6rem;
  }

  @include mq(sm) {
    &__sup {
      font-size: 1rem;
    }
    &__main {
      font-size: 2rem;
    }
    &__mainSm {
      font-size: 1.4rem;
    }
  }
}

/*--------------------------------------------
  sup horizon
--------------------------------------------*/
.c-ttl_supHorizon {
  display: flex;
  align-items: center;
  margin-bottom: 0.8em;
  line-height: 1.3;

  &__title {
    font-weight: 400;
    font-size: 2.4rem;
  }
  &__sup {
    margin-left: 1em;
    font-weight: 500;
    font-size: 1.2rem;
    color: $main;
  }

  @include mq(sm) {
    &__title {
      font-size: 2rem;
    }
    &__sup {
      margin-left: 0.8em;
      font-size: 1.4rem;
    }
  }
}

/*--------------------------------------------
  sup vertical
--------------------------------------------*/
.c-ttl_supVertical {
  $amp: &;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;

  &__sup {
    position: relative;
    margin-left: 3em;
    padding-left: 0.8em;
    padding-bottom: 0.5em;
    font-weight: 500;
    font-size: 1.2rem;
    color: $main;
    line-height: 1.3;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 1px;
      height: 10rem;
      background-color: $black_l;
    }
  }

  &__title {
    padding-top: 0.3em;
    font-weight: 500;
    font-size: 4rem;
    line-height: 1.3;

    &.-bdr {
      position: relative;
      padding-top: 0;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 1px;
        height: 10rem;
        background-color: $black_l;
      }
    }
  }

  .-en & {
    #{$amp}__sup {
      font-weight: 400;
      font-size: 1.6rem;
      line-height: 1.1;
    }

    #{$amp}__title {
      font-weight: 400;
      font-size: 4.6rem;
      line-height: 1.1;
    }
  }

  @include mq(sm) {
    &__sup {
      margin-left: 1.5em;
      padding-left: 0.4em;
      font-size: 1.4rem;

      &::before {
        height: 12rem;
      }
    }

    &__title {
      font-size: 3rem;

      &.-bdr {
        &::before {
          height: 12rem;
        }
      }
    }

    .-en & {
      #{$amp}__title {
        font-size: 3.6rem;
      }
    }
  }
  @include mq(xs) {
    align-items: center;
    -ms-writing-mode: lr-tb;
    writing-mode: horizontal-tb;
    text-align: center;

    &__sup {
      margin-left: 0;
      padding-left: 0;
      padding-bottom: 0;
      font-size: 1.4rem;

      &::before {
        display: none;
      }
    }

    &__title {
      font-size: 2.4rem;

      &.-bdr {
        &::before {
          display: none;
        }
      }
    }

    .-en & {
      #{$amp}__title {
        font-size: 3rem;
      }
    }
  }
}

/*--------------------------------------------
  strong
--------------------------------------------*/
.c-ttl_strong {
  margin-bottom: 1.2em;
  font-weight: 500;
  font-size: 2.4rem;
  line-height: 1.5;

  &.-lg {
    font-size: 3rem;
  }

  @include mq(sm) {
    margin-bottom: 1em;
    font-size: 2rem;

    &.-lg {
      font-size: 2.4rem;
    }
  }
}

/*--------------------------------------------
  border bottom
--------------------------------------------*/
.c-ttl_bdb {
  position: relative;
  margin-bottom: 1.2em;
  padding-bottom: 0.8em;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 1.3;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 1.25em;
    height: 1px;
    background-color: $black_l;
  }

  &.-xs {
    font-size: 1.8rem;
  }

  @include mq(sm) {
    font-size: 2rem;

    &::before {
      width: 1.25em;
    }

    &.-xs {
      font-size: 1.6rem;
    }
  }
}

/*--------------------------------------------
  editor
--------------------------------------------*/
.c-ttl_editor {
  margin-bottom: 3em;
  padding-bottom: 1em;
  border-bottom: 1px solid $bd;

  &__info {
    display: flex;
    align-items: center;
    margin-bottom: 0.8em;
  }
  &__infoDate {
    font-size: 1.4rem;
    line-height: 1.3;
  }
  &__infoLabelWrap {
    margin-left: 2em;
  }

  &__title {
    font-weight: 500;
    font-size: 3rem;
    line-height: 1.3;
  }

  @include mq(sm) {
    margin-bottom: 2em;
    padding-bottom: 0.8em;

    &__info {
      margin-bottom: 0.6em;
    }
    &__infoLabelWrap {
      margin-left: 1.5em;
    }

    &__title {
      font-size: 2.4rem;
    }
  }
}
