/*--------------------------------------------
  left gap
--------------------------------------------*/
.c-box_leftGap {
  padding-left: 12.5em;

  @include mq(sm) {
    padding-left: 10em;
  }
  @include mq(xs) {
    padding-left: 0;
  }
}

/*--------------------------------------------
  border bottom
--------------------------------------------*/
.c-box_bdBottom {
  &__item {
    padding-bottom: 3em;
    border-bottom: 1px solid $bd;

    & + & {
      margin-top: 3em;
    }
  }

  @include mq(sm) {
    &__item {
      padding-bottom: 2em;

      & + & {
        margin-top: 2em;
      }
    }
  }
}

/*--------------------------------------------
  border bottom
--------------------------------------------*/
.c-box_color {
  padding: 2em;
  background-color: $gray_l;

  &.-lg {
    padding: 5em;
  }

  &.-beige {
    background-color: $beige;
  }

  @include mq(sm) {
    padding: 2em 1.5em;

    &.-lg {
      padding: 3em;
    }
  }

  @include mq(xs) {
    &.-lg {
      padding: 2em 1.5em;
    }
  }
}
