/*--------------------------------------------
  Wordpress EDITOR
--------------------------------------------*/
.wp-editor {
  > * {
    & + * {
      margin-top: 3em;
    }
  }

  .wp-block-columns {
    .wp-block-column {
      &:not(:last-child) {
        margin-bottom: 2em;
      }

      > * {
        & + * {
          margin-top: 2em;
        }
      }
    }
  }

  strong {
    font-weight: 700;
  }
  em {
    font-style: italic;
  }
  blockquote {
    display: block;
    padding: 2em;
    background-color: $gray_l;
  }
  sup {
    vertical-align: super;
    font-size: 70%;
  }
  sub {
    vertical-align: sub;
    font-size: 70%;
  }
  figure {
    max-width: 100%;
  }
  img[class*="wp-image-"],
  img[class*="attachment-"] {
    max-width: 100%;
    height: auto;
  }

  .alignleft {
    float: none;
    margin: 0;
    margin-right: auto;
  }
  .aligncenter {
    float: none;
    margin: 0 auto;
  }
  .alignright {
    float: none;
    margin: 0;
    margin-left: auto;
  }

  .c-iframe {
    padding-top: 52.5%;
  }


  h2 {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.3;
    letter-spacing: .1rem;
  }
  h3 {
    font-weight: 500;
    font-size: 2rem;
    line-height: 1.3;
    letter-spacing: .1rem;
  }
  h4 {
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 1.3;
    letter-spacing: .1rem;
  }


  a:not([class]) {
    color: $link;
    text-decoration: underline;
    word-break: break-all;

    &:hover {
      text-decoration: none;
    }
  }


  ul:not([class]),
  ol:not([class]) {
    padding-left: 2em;
    line-height: 1.5;

    > li {
      &:not(:first-child) {
        margin-top: .8em;
      }

      > ul,
      > ol {
        margin-top: .6em;
      }
    }
  }
  ul:not([class]) {
    > li {
      list-style-type: disc;
    }
  }
  ol:not([class]) {
    > li {
      list-style-type: decimal;
    }
  }


  table {
    width: 100%;

    tr {
      > th,
      > td {
        padding: 1em;
        border: 1px solid $bd;
      }
      > th {
        background-color: $gray_l;
      }
    }
  }


  @include mq(sm) {
    > * {
      & + * {
        margin-top: 2em;
      }
    }

    .wp-block-columns {
      .wp-block-column {
        &:not(:last-child) {
          margin-bottom: 1.5em;
        }

        > * {
          & + * {
            margin-top: 1.5em;
          }
        }
      }
    }

    blockquote {
      padding: 1.5em;
    }

    h2 {
      font-size: 2rem;
    }
    h3 {
      font-size: 1.8rem;
    }
    h4 {
      font-size: 1.6rem;
    }
  }
}

