/*--------------------------------------------
  Error
--------------------------------------------*/
.mw_wp_form {
  .error {
    margin-top: 1em;
    padding: 0.4em 0.8em;
    background-color: $gray_l;
    font-weight: 400;
    color: $red;
  }
  .error_exists {
    margin-bottom: 1em;
    padding: 0.4em 0.8em;
    background-color: $red;
    font-weight: 400;
    color: $white;
  }
}

/*--------------------------------------------
  field size
--------------------------------------------*/
.c-form_ss {
  max-width: 10rem;

  @include mq(ss) {
    max-width: 100%;
  }
}
.c-form_sm {
  max-width: 25rem;

  @include mq(ss) {
    max-width: 100%;
  }
}
.c-form_md {
  max-width: 50rem;
}

/*--------------------------------------------
  supplement
--------------------------------------------*/
.c-form_sup {
  margin-top: 0.4em;
  font-size: 1.4rem;
}

/*--------------------------------------------
  required
--------------------------------------------*/
.c-form_req {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__label {
    display: block;
    margin-left: 2em;
    padding: 0.6em 1em;
    border: 1px solid $red;
    background-color: $red;
    text-align: center;
    font-weight: 500;
    font-size: 80%;
    color: $white;
    line-height: 1;
    letter-spacing: 0.1rem;

    &.-not {
      border-color: $bd;
      background-color: $gray_l;
      color: $point;
    }
  }
}

/*--------------------------------------------
  unit
--------------------------------------------*/
.c-form_unit {
  &__item {
    & + & {
      margin-top: 1.2em;
    }
  }
  &__fx {
    display: flex;
    align-items: center;
    margin-left: -0.8em;
  }
  &__unit {
    padding-left: 0.8em;
    white-space: nowrap;
  }
  &__conts {
    padding-left: 0.8em;
  }
  &__error {
    &:not(:empty) {
      margin-top: 0.4em;
    }
  }
}

/*--------------------------------------------
  label
--------------------------------------------*/
.c-form_label {
  $amp: &;
  &__item {
    display: flex;
    flex-wrap: wrap;

    & + & {
      margin-top: 1.2em;
    }
  }
  &__label {
    width: 16rem;
    padding-top: 0.6em;
    text-align: right;

    .mw_wp_form_confirm & {
      padding-top: 0;
    }
  }
  &__conts {
    flex: 1;
    padding-left: 1em;
  }

  @include mq(sm) {
    &__label {
      width: 100%;
      padding-top: 0;
      text-align: left;
    }
    &__conts {
      width: 100%;
      margin-top: 0.5em;
      padding-left: 0;
    }
  }
}

/*--------------------------------------------
  checkbox & radio
--------------------------------------------*/
.c-form_radio,
.c-form_cb {
  display: flex;
  flex-wrap: wrap;
  line-height: 2;

  .mw_wp_form_input & {
    margin-left: -2em;

    > * {
      margin-left: 2em !important;
    }
  }

  &.-vertical {
    flex-direction: column;
    align-items: flex-start;
  }

  label {
    display: block;
    cursor: pointer;

    > input {
      position: absolute;
      visibility: hidden;

      & + span {
        position: relative;
        display: inline-block;

        &::before {
          content: "";
          display: block;
          position: absolute;
        }
      }

      &:checked + span::after {
        content: "";
        display: block;
        position: absolute;
      }
    }
  }
}

.c-form_radio {
  label {
    > input {
      & + span {
        padding-left: 1.8em;

        &::before {
          top: 0.5em;
          left: 0;
          width: 1.125em;
          height: 1.125em;
          border-radius: 50%;
          border: 1px solid $bd_d;
          background-color: $gray_l;
        }
      }

      &:checked + span::after {
        top: 0.8em;
        left: 0.3125em;
        width: 0.5em;
        height: 0.5em;
        border-radius: 50%;
        background-color: $main;
      }
    }
  }
}

.c-form_cb {
  label {
    > input {
      & + span {
        padding-left: 2.8rem;

        &::before {
          top: 0.45em;
          left: 0;
          width: 1.1em;
          height: 1.1em;
          border: 1px solid $bd_d;
          background-color: $gray_l;
        }
      }

      &:checked + span::after {
        top: 0.6em;
        left: 0.2em;
        width: 1.2em;
        height: 0.5em;
        border-left: 0.3rem solid $main;
        border-bottom: 0.3rem solid $main;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
    }
  }
}

/*--------------------------------------------
  Agree checkbox
--------------------------------------------*/
.c-form_agree {
  display: flex;
  justify-content: center;
  line-height: 1.3;
  letter-spacing: 0.1rem;

  label {
    > input {
      position: absolute;
      visibility: hidden;

      & + span {
        position: relative;
        display: inline-block;
        padding-left: 1.8em;

        &::before {
          content: "";
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 1.25em;
          height: 1.25em;
          border: 1px solid $bd_d;
          background-color: $gray_l;
        }
      }

      &:checked + span::after {
        display: block;
        content: "";
        position: absolute;
        top: 0.1875em;
        left: 0.1875em;
        width: 1.125em;
        height: 0.6em;
        border-left: 0.3rem solid $main;
        border-bottom: 0.3rem solid $main;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
    }
  }
}
