/*--------------------------------------------
  loader
--------------------------------------------*/
.p-idx_loaderSec {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $point;
  transition: opacity 1.4s;

  .c-logo {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
  }

  &.is-loaded {
    pointer-events: none;
    opacity: 0;
  }
}

/*--------------------------------------------
  key
--------------------------------------------*/
@keyframes scale {
  0% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.p-idx_kvSec {
  position: relative;
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden;

  &__slider {
    width: 100%;
    height: 100%;

    .slick-list {
      height: 100%;

      &:first-child {
        transform: translate3d(0, 0, 0) !important;

        .slick-track {
          transform: translate3d(0, 0, 0) !important;
        }
      }

      .slick-track {
        display: flex;
        height: 100%;

        .slick-slide {
          float: none;
          height: 100%;

          .c-ofi {
            height: 100%;
            padding-top: 0;
            transform: scale(1);
          }

          &.slick-current {
            .c-ofi {
              animation: scale 6.5s ease forwards;
            }
          }
        }
      }
    }
  }

  &__logo {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
  }

  @include mq(sm) {
    height: 120vw;
  }
}
