/*--------------------------------------------
  content main
--------------------------------------------*/
.l-content {
  overflow: hidden;

  .l-page.-under & {
    padding-top: 11.8rem;
  }

  @include mq(sm) {
    .l-page.-under & {
      display: flex;
      flex-direction: column;
      padding-top: 7rem;

      &__bc {
        order: 2;
      }
    }
  }
}
