/*--------------------------------------------
  column
--------------------------------------------*/
.c-col {
  $amp: &;
  display: flex;
  flex-wrap: wrap;
  margin-top: -3em;
  margin-left: -3em;

  &__item {
    width: 50%;
    margin-top: 3em;
    padding-left: 3em;
  }

  &.-gap_xs {
    margin-top: -1.2em;

    > #{$amp}__item {
      margin-top: 1.2em;
    }
  }
  &.-gutter_xs {
    margin-left: -1.2em;

    > #{$amp}__item {
      padding-left: 1.2em;
    }
  }
  &.-gutter_xl {
    margin-left: -7.5em;

    > #{$amp}__item {
      padding-left: 7.5em;
    }
  }
  &.-space_ss {
    margin-top: -0.8em;
    margin-left: -0.8em;

    > #{$amp}__item {
      margin-top: 0.8em;
      padding-left: 0.8em;
    }
  }
  &.-space_sm {
    margin-top: -2em;
    margin-left: -2em;

    > #{$amp}__item {
      margin-top: 2em;
      padding-left: 2em;
    }
  }
  &.-space_lg {
    margin-top: -5.5em;
    margin-left: -5.5em;

    > #{$amp}__item {
      margin-top: 5.5em;
      padding-left: 5.5em;
    }
  }

  &.-col3 {
    @include mq(xs-) {
      > #{$amp}__item {
        width: 33.3333%;
      }
    }
  }

  &.-top_news {
    @include mq(sm-) {
      > #{$amp}__item {
        &:nth-child(n + 4) {
          display: none;
        }
      }
    }
  }

  &.-sdgs {
    margin-top: -5em;
    margin-left: 0;

    > #{$amp}__item {
      margin-top: 5em;
      padding-left: 0;

      &:nth-child(even) {
        padding-left: 7.5em;
      }
    }
  }
  &.-products {
    flex-direction: column;
    align-items: center;
    margin-top: -5em;
    margin-left: 0;

    > #{$amp}__item {
      width: 38rem;
      max-width: 100%;
      margin-top: 5em;
      padding-left: 0;
    }
  }

  @include mq(sm) {
    @include mq(xs-) {
      &.-col2_sm {
        > #{$amp}__item {
          width: 50%;
        }
      }
    }

    margin-top: -2em;
    margin-left: -2em;

    &__item {
      margin-top: 2em;
      padding-left: 2em;
    }

    &.-gap_xs {
      margin-top: -1em;

      > #{$amp}__item {
        margin-top: 1em;
      }
    }
    &.-gutter_xs {
      margin-left: -1em;

      > #{$amp}__item {
        padding-left: 1em;
      }
    }
    &.-gutter_xl {
      margin-left: -3.5em;

      > #{$amp}__item {
        padding-left: 3.5em;
      }
    }
    &.-space_sm {
      margin-top: -1.2em;
      margin-left: -1.2em;

      > #{$amp}__item {
        margin-top: 1.2em;
        padding-left: 1.2em;
      }
    }
    &.-space_lg {
      margin-top: -3em;
      margin-left: -3em;

      > #{$amp}__item {
        margin-top: 3em;
        padding-left: 3em;
      }
    }

    &.-col2_sp {
      > #{$amp}__item {
        width: 50%;
      }
    }

    &.-sdgs {
      margin-top: -3em;
      margin-left: 0;

      > #{$amp}__item {
        margin-top: 3em;
        padding-left: 0;

        &:nth-child(odd) {
          width: 60%;
        }
        &:nth-child(even) {
          width: 40%;
          padding-left: 3em;
        }
      }
    }
    &.-products {
      margin-top: -3em;

      > #{$amp}__item {
        margin-top: 3em;
      }
    }
  }
  @include mq(xs) {
    &__item {
      width: 100%;
    }

    &.-col_xs {
      > #{$amp}__item {
        width: 100%;
      }
    }

    &.-gutter_xl {
      margin-left: -2em;

      > #{$amp}__item {
        padding-left: 2em;
      }
    }
    &.-space_lg {
      margin-top: -2em;
      margin-left: -2em;

      > #{$amp}__item {
        margin-top: 2em;
        padding-left: 2em;
      }
    }
  }
  @include mq(ss) {
    &.-col3_ss {
      > #{$amp}__item {
        width: 33.3333%;
      }
    }

    &.-sdgs {
      > #{$amp}__item {
        &:nth-child(odd) {
          width: 100%;
        }
        &:nth-child(even) {
          width: 100%;
          margin-top: 2em;
          padding: 0;
        }
      }
    }
  }
}
