/*--------------------------------------------
  button wrap
--------------------------------------------*/
.c-btn_wrap {
  display: flex;
  justify-content: center;
  margin-top: 3em;

  &.-col {
    > * + * {
      margin-left: 1.5em;
    }
  }

  &.-mgt_lg {
    margin-top: 5em;
  }

  @include mq(sm) {
    margin-top: 2em;

    &.-mgt_lg {
      margin-top: 3.5em;
    }
  }
  @include mq(xs) {
    &.-col {
      flex-direction: column;
      align-items: center;

      > * + * {
        margin-top: 1.5em;
        margin-left: 0;
      }
    }
  }
}

/*--------------------------------------------
  button
--------------------------------------------*/
.c-btn {
  display: block;
  width: 12.5em;
  max-width: 100%;
  padding: 0.8em;
  border: 1px solid $black_l;
  text-align: center;
  color: $font;
  line-height: 1.3;

  &:hover {
    border-color: $main;
    background-color: $main;
    color: $white;
  }

  &.-sm {
    width: 10em;
    font-size: 1.4rem;
  }
  &.-md {
    width: 14em;
    padding: 1.2em;
    font-size: 1.8rem;
  }

  &.-back {
    border: 1px solid $bd;
    background-color: $gray_l;

    &:hover {
      background-color: $gray;
      color: $font;
    }
  }

  @include mq(sm) {
    &.-md {
      padding: 1em;
      font-size: 1.6rem;
    }
  }
}
