/*--------------------------------------------
  key
--------------------------------------------*/
.p-under_kv {
  $amp: &;
  position: relative;

  &__pic {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 0;

    &.-commitment,
    &.-catalog {
      > img {
        object-position: 85% center;
        font-family: "object-fit: cover; object-position: 85% center;";
      }
    }
    &.-health {
      > img {
        object-position: 75% center;
        font-family: "object-fit: cover; object-position: 75% center;";
      }
    }
    &.-right,
    &.-right_sm {
      > img {
        object-position: right center;
        font-family: "object-fit: cover; object-position: right center;";
      }
    }
  }
  &__inr {
    z-index: 1;
    position: relative;
    display: flex;
    align-items: center;
    height: 40rem;
  }

  &__titleWrap {
    display: flex;
    flex-direction: column;
    color: $white;
    text-shadow: 0 0 0.5em rgba($font, 0.4);
  }
  &__titleMain {
    font-weight: 500;
    font-size: 3rem;
    line-height: 1.3;

    .-en & {
      font-weight: 400;
      font-size: 4rem;
      line-height: 1.1;
    }
  }
  &__titleSub {
    position: relative;
    margin-top: 1em;
    padding-left: 2.5em;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 1.3;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 1.5em;
      height: 1px;
      margin: auto;
      background-color: $white;
    }
  }

  &.-black {
    #{$amp}__titleWrap {
      color: $font;
      text-shadow: 0 0 0.5em rgba($white, 0.4);
    }
    #{$amp}__titleSub {
      &::before {
        background-color: $font;
      }
    }
  }

  @include mq(sm) {
    &__pic {
      &.-company {
        > img {
          object-position: 35% center;
          font-family: "object-fit: cover; object-position: 35% center;";
        }
      }
      &.-catalog {
        > img {
          object-position: 77% center;
          font-family: "object-fit: cover; object-position: 77% center;";
        }
      }
    }
    &__inr {
      height: 26rem;
    }

    &__titleMain {
      font-size: 2.4rem;

      .-en & {
        font-size: 3.4rem;
      }
    }
    &__titleSub {
      margin-top: 0.8em;
      padding-left: 2em;
      font-size: 1.6rem;

      &::before {
        width: 1.2em;
      }
    }
  }
  @include mq(xs) {
    &__pic {
      &.-health {
        > img {
          object-position: 68% center;
          font-family: "object-fit: cover; object-position: 68% center;";
        }
      }
      &.-right {
        > img {
          object-position: 60% center;
          font-family: "object-fit: cover; object-position: 60% center;";
        }
      }
      &.-right_sm {
        > img {
          object-position: 85% center;
          font-family: "object-fit: cover; object-position: 85% center;";
        }
      }
    }

    &__titleMain {
      .-en & {
        font-size: 3rem;
      }
    }
  }
  @include mq(ss) {
    &__pic {
      &.-health {
        > img {
          object-position: left center;
          font-family: "object-fit: cover; object-position: left center;";
        }
      }
    }
    &__inr {
      height: 22rem;
    }
  }
}

