/*--------------------------------------------
  pagination
--------------------------------------------*/
.c-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4em;

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.5em;
    height: 2.5em;
    line-height: 1;

    & + * {
      margin-left: 1em;
    }

    &.-link {
      padding: .3em;
      background-color: $white;
      border: 1px solid $main;
      color: $main;

      &:hover {
        color: $white;
        background-color: $main;
        opacity: $opacity;
      }
    }

    &.-current {
      padding: .3em;
      background-color: $main;
      border: 1px solid $main;
      color: $white;
    }
  }


  @include mq(sm) {
    margin-top: 3em;

    &__item {
      & + * {
        margin-left: .6em;
      }

      &.-ellipsis {
        width: 1.5em;
        height: 1.5em;
      }
    }
  }
}
