@charset "utf-8";

/*!------------------------------------*\
    slideout
\*!------------------------------------*/
@include mq(sm) {
  .slideout-open,
  .slideout-open body {
    overflow: hidden;
  }

  .slideout-menu {
    z-index: 101;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
    transition: opacity 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  }
  .slideout-menu::before {
    content: "";
    display: block;
    position: absolute;
    top: -6em;
    left: 0;
    right: 0;
    bottom: -6em;
    background-color: $main;
  }
  .slideout-open .slideout-menu {
    opacity: 1;
    pointer-events: auto;
  }
  .slideout-menu .scroll-inner {
    position: relative;
    height: 100vh;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
  .slideout-menu .menu-inner {
    padding: 8em $gutter 10em;
  }
}

