/*--------------------------------------------
  table responsive
--------------------------------------------*/
.c-tbl_responsive {
  min-height: 0.01%;
  overflow-x: auto;

  @include mq(sm) {
    width: 100%;
    overflow-y: hidden;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    scrollbar-width: 0.2em;
    scrollbar-color: $main $gray_l;
    &::-webkit-scrollbar {
      height: 0.2em;
    }
    &::-webkit-scrollbar-track {
      background: $gray_l;
      border-radius: 1rem;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 1rem;
      background: $main;
    }

    & > table {
      margin-bottom: 0;
    }

    & > table > thead > tr > th,
    & > table > tbody > tr > th,
    & > table > tfoot > tr > th,
    & > table > thead > tr > td,
    & > table > tbody > tr > td,
    & > table > tfoot > tr > td {
      white-space: nowrap;
    }
  }
}

/*--------------------------------------------
  table
--------------------------------------------*/
.c-tbl {
  tr {
    border-bottom: 1px solid $bd;

    > th,
    > td {
      padding: 1.5em 3em;
    }
    > th {
      width: 16rem;
      padding-right: 0;
      font-weight: 500;
      text-align: left;
      white-space: nowrap;
    }
  }

  @include mq(sm) {
    tr {
      > th,
      > td {
        padding: 1.2em 2em;
      }
      > th {
        width: 14rem;
        padding-right: 0;
      }
    }
  }
  @include mq(xs) {
    tr {
      > th,
      > td {
        padding: 1em 1.2em;
      }
      > th {
        width: 12rem;
        padding-right: 0;
      }
    }

    &.-block_sp {
      tr {
        > th,
        > td {
          display: block !important;
          width: 100% !important;
        }
        > th {
          padding-right: 1.2em;
          padding-bottom: 0;
        }
      }
    }
  }
}
