/*--------------------------------------------
  header main
--------------------------------------------*/
.l-header {
  z-index: 105;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: $white;
  white-space: nowrap;

  .l-page:not(.-under) & {
    transform: translateY(-105%);
    transition: transform 0.4s;

    &.is-view {
      transform: translateY(0%);
    }
  }

  @include mq(sm-) {
    display: flex;
    align-items: center;
    padding: 1.8em;

    &__logoWrap {
      flex-grow: 1;
      padding-right: 2em;
    }
    &__logo {
      display: flex;
      align-items: center;

      > a {
        display: block;

        &:hover {
          opacity: $opacity;
        }
      }
    }
  }
  @include mq(sm) {
    &__logoWrap {
      z-index: 105;
      position: relative;
      display: flex;
      align-items: center;
      padding: 1em 5%;
      background-color: $white;
    }
    &__logo {
      flex-grow: 1;
      display: flex;
      align-items: center;
      padding-right: 2em;

      > a {
        display: block;

        &:hover {
          opacity: $opacity;
        }
      }
    }
  }
}

.l-header_gnavLayout {
  @include mq(sm-) {
    display: flex;
    align-items: center;
  }
  @include mq(sm) {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__menuFrame {
      width: 100%;
    }

    &__snsFrame,
    &__langFrame {
      margin-top: 2em;
    }
  }
}

.l-header_gnav {
  $amp: &;
  @include mq(sm-) {
    display: flex;

    &__item {
      position: relative;

      &.-sub_menu {
        @include transition;

        &:hover {
          background-color: $main;
          color: $white;

          #{$amp}__subMenu {
            opacity: 1;
            pointer-events: auto;
          }
        }
      }
    }

    &__name,
    &__anc {
      display: block;
      padding: 1em 1.2em;
      line-height: 1.3;

      .-en & {
        padding: 0.8em 1em;
        font-size: 2rem;
      }
    }
    &__anc {
      &:hover {
        background-color: $sub;
        color: $white;
      }
    }

    &__subMenu {
      opacity: 0;
      pointer-events: none;
      z-index: 1;
      position: absolute;
      left: 0;
      // right: 0;
      min-width: 21rem;
      background-color: $main;
      @include transition(opacity);
    }
    &__subMenuAnc {
      display: block;
      padding: 0.8em 1.2em;
      color: $white;
      line-height: 1.3;

      &:hover {
        background-color: $sub;
      }
    }
  }
  @include mq(sm) {
    &__item {
      border-bottom: 1px solid $sub;

      &.-sub_menu {
        #{$amp}__name {
          position: relative;

          &::before,
          &::after {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            right: 1em;
            bottom: 0;
            margin: auto;
            width: 1em;
            height: 2px;
            background-color: $white;
            transition: 0.4s;
          }
          &::after {
            transform: rotate(90deg);
          }

          &.is-act {
            background-color: $sub;

            &::before {
              opacity: 0;
            }
            &::after {
              transform: rotate(0);
            }
          }
        }
      }
    }

    &__name,
    &__anc {
      display: block;
      padding: 1em 1.2em;
      padding-right: 2.5em;
      font-size: 1.6rem;
      color: $white;
      line-height: 1.3;

      .-en & {
        font-size: 2.2rem;
      }
    }
    &__anc {
      &:hover {
        background-color: $sub;
      }
    }

    &__subMenu {
      display: none;
      padding: 1.5em;
      padding-right: 2em;
      background-color: rgba($white, 0.8);
    }
    &__subMenuItem {
      & + * {
        margin-top: 0.5em;
      }
    }
    &__subMenuAnc {
      display: block;
      padding: 0.5em;
      font-size: 1.6rem;
      line-height: 1.3;

      &:hover {
        opacity: $opacity;
      }
    }
  }
}
