/*--------------------------------------------
  inner
--------------------------------------------*/
.c-inner {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: $gutter;
  padding-right: $gutter;

  &.-gutter0 {
    padding-left: 0;
    padding-right: 0;
  }

  @include mq(sm-) {
    max-width: calc(120rem + (#{$gutter} * 2));

    &.-full {
      max-width: 100%;
    }

    &.-gutter0 {
      padding-left: 0;
      padding-right: 0;

      &.-ss {
        max-width: 60rem;
      }
      &.-xs {
        max-width: 80rem;
      }
      &.-sm {
        max-width: 100rem;
      }
      // &.-md {
      //   max-width: 140rem;
      // }
      // &.-lg {
      //   max-width: 160rem;
      // }
    }

    &.-ss {
      max-width: calc(60rem + (#{$gutter} * 2));
    }
    &.-xs {
      max-width: calc(80rem + (#{$gutter} * 2));
    }
    &.-sm {
      max-width: calc(100rem + (#{$gutter} * 2));
    }
    // &.-md {
    //   max-width: calc( 140rem + (#{$gutter} * 2) );
    // }
    // &.-lg {
    //   max-width: calc( 160rem + (#{$gutter} * 2) );
    // }
  }
}

/*--------------------------------------------
  box padding
--------------------------------------------*/
.c-boxpd {
  @include mq(sm-) {
    padding-top: 6.25em;
    padding-bottom: 6.25em;

    &_sm {
      padding-top: 5em;
      padding-bottom: 5em;
    }
    // &_md {
    //   padding-top: 7.5em;
    //   padding-bottom: 7.5em;
    // }
    &_lg {
      padding-top: 9.4em;
      padding-bottom: 9.4em;
    }
  }
  @include mq(sm) {
    padding-top: 4em;
    padding-bottom: 4em;

    &_sm {
      padding-top: 3.5em;
      padding-bottom: 3.5em;
    }
    // &_md {
    //   padding-top: 5em;
    //   padding-bottom: 5em;
    // }
    &_lg {
      padding-top: 6em;
      padding-bottom: 6em;
    }
  }
}

/*--------------------------------------------
  iframe responsive
--------------------------------------------*/
.c-iframe {
  position: relative;
  padding-top: 60%;
  height: 0;
  overflow: hidden;

  &.-youtube {
    padding-top: 56.25%;
  }

  iframe {
    position: absolute;
    top: 0;
    right: 0;
    width: 100% !important;
    height: 100% !important;
  }

  @include mq(xs) {
    padding-top: 65%;
  }
}

/*--------------------------------------------
  object-fit
--------------------------------------------*/
.c-ofi {
  position: relative;
  padding-top: 60%;
  backface-visibility: hidden;

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    max-width: none;
    max-height: none;
    object-fit: cover;
    object-position: center center;
    font-family: "object-fit: cover; object-position: center center;";

    &.-top {
      object-position: center top;
      font-family: "object-fit: cover; object-position: center top;";
    }
    &.-left {
      object-position: left center;
      font-family: "object-fit: cover; object-position: left center;";
    }
    &.-right {
      object-position: right center;
      font-family: "object-fit: cover; object-position: right center;";
    }
    &.-bottom {
      object-position: center bottom;
      font-family: "object-fit: cover; object-position: center bottom;";
    }

    &.-contain {
      margin: auto;
      object-fit: contain;
      font-family: "object-fit: contain; object-position: center center;";
    }
  }
}
