/*--------------------------------------------
  breadcrumb ol
--------------------------------------------*/
.l-breadcrumb_list {
  display: flex;
  flex-wrap: wrap;
  padding: 1.5em 0 0.8em;

  &__item {
    position: relative;

    & + * {
      &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: -0.3em;
        bottom: 0;
        width: 0.45em;
        height: 0.45em;
        margin: auto;
        border-right: 1px solid $font;
        border-bottom: 1px solid $font;
        transform: rotate(-45deg);
      }
    }

    > a {
      display: block;
      padding: 0.3em 1em;
      font-size: 1.4rem;
      color: $font;
      line-height: 1.3;
      letter-spacing: 0.1rem;

      &:hover {
        text-decoration: underline;
      }
    }

    &:first-child {
      > a {
        padding-left: 0;
      }
    }

    &:last-child {
      > a {
        text-decoration: none;
        pointer-events: none;
      }
    }
  }

  @include mq(sm) {
    padding: 1em 0;
