/*--------------------------------------------
  section layout
--------------------------------------------*/
.p-obj_secLayout {
  $amp: &;
  display: flex;
  flex-direction: row-reverse;
  margin: 0 -1.5em;

  &__titleFrame {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;
    width: 12.5em;
    margin-top: -3em;
    padding: 0 1.5em;

    &.-mgt0 {
      margin-top: 0;
    }
  }
  &__titleFrameInr {
    width: 100%;
  }

  &__contsFrame {
    flex: 1;
    padding: 0 1.5em;
  }

  &__catchImg {
    margin-left: -12.5em;

    .c-ofi {
      padding-top: 45%;
    }

    &.-md {
      .c-ofi {
        padding-top: 60%;
      }
    }
  }

  &.-reverse {
    flex-direction: row;

    #{$amp}__titleFrame {
      flex-direction: row;
    }

    #{$amp}__catchImg {
      margin-left: 0;
      margin-right: -12.5em;
    }
  }

  @include mq(lg) {
    &__catchImg {
      margin-left: -5vw;
    }

    &.-reverse {
      #{$amp}__catchImg {
        margin-left: 0;
        margin-right: -5vw;
      }
    }
  }
  @include mq(sm) {
    margin: 0 -1em;

    &__titleFrame {
      width: 10em;
      margin-top: -2em;
      padding: 0 1em;
    }

    &__contsFrame {
      padding: 0 1em;
    }
  }
  @include mq(xs) {
    flex-direction: column;

    &__titleFrame {
      display: block;
      width: 100%;
      margin-top: 0;
      padding: 0 2em;
    }
    &__contsFrame {
      margin-top: 2.5em;

      &.-mgt_lg {
        margin-top: 5em;
      }
    }

    &.-reverse {
      flex-direction: column;
    }
  }
}

/*--------------------------------------------
  alternate
--------------------------------------------*/
.p-obj_alternate {
  $amp: &;

  &__item {
    display: flex;
    align-items: flex-start;
    margin: 0 -2.5em;

    & + & {
      margin-top: 8em;
    }

    &:nth-child(odd) {
      flex-direction: row-reverse;
    }
  }

  &__conts {
    width: 39%;
    margin-top: 3em;
    padding: 0 2.5em;

    &.-full {
      width: 100%;
    }
  }
  &__imgWrap {
    flex: 1;
    padding: 0 2.5em;
  }
  &__pic {
    padding-top: 76%;
  }

  &.-ai_center {
    #{$amp}__item {
      align-items: center;
    }
    #{$amp}__conts {
      margin-top: 0;
    }
  }
  &.-type_half {
    #{$amp}__item {
      align-items: center;
      margin-left: -3em;
      margin-right: -3em;
    }
    #{$amp}__conts {
      width: calc(50% - 3em);
      margin-top: 0;
      padding: 0 3em;
    }
    #{$amp}__imgWrap {
      padding: 0 3em;
    }
    #{$amp}__pic {
      padding-top: 86%;
    }
  }

  &.-reverse {
    @include mq(xs-) {
      #{$amp}__item {
        &:nth-child(odd) {
          flex-direction: row;
        }
        &:nth-child(even) {
          flex-direction: row-reverse;
        }
      }
    }
  }

  @include mq(sm) {
    &__item {
      margin: 0 -1em;

      & + & {
        margin-top: 5em;
      }
    }

    &__conts {
      width: 45%;
      margin-top: 0;
      padding: 0 1em;
    }
    &__imgWrap {
      padding: 0 1em;
    }
    &__pic {
      padding-top: 76%;
    }

    &.-type_half {
      #{$amp}__item {
        align-items: stretch;
        margin-left: -1.2em;
        margin-right: -1.2em;
      }
      #{$amp}__conts {
        width: calc(50% - 1.2em);
        padding: 0 1.2em;
      }
      #{$amp}__imgWrap {
        padding: 0 1.2em;
      }
    }
  }
  @include mq(xs) {
    &__item {
      flex-direction: column;

      &:nth-child(odd) {
        flex-direction: column;
      }
    }

    &__conts {
      width: 100%;
    }
    &__imgWrap {
      width: 100%;
      margin-top: 2em;
    }

    &.-type_half {
      #{$amp}__conts {
        width: 100%;
      }
    }
  }
}

/*--------------------------------------------
  description
--------------------------------------------*/
.p-obj_description {
  &__item {
    display: flex;
    flex-direction: row-reverse;
    padding: 2em;
    border: 1px solid $bd;

    & + & {
      margin-top: 1.2em;
    }
  }
  &__contsFrame {
    flex: 1;
    position: relative;
    padding-right: 14rem;
  }
  &__more {
    position: absolute;
    right: 0;
    display: block;
    padding: 0.3em;
    padding-left: 1.8em;
    line-height: 1.1;
    transform: translateY(-100%);

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      width: 1.125em;
      height: 1.125em;
      background-color: $point;
      border-radius: 50%;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0.375em;
      bottom: 0;
      margin: auto;
      width: 0.375em;
      height: 0.375em;
      border-right: 1px solid $white;
      border-bottom: 1px solid $white;
      transform: rotate(45deg);
      transition: transform 0.4s;
    }

    &:hover {
      text-decoration: underline;
    }

    &.is-act {
      &::after {
        transform: rotate(225deg);
      }
    }
  }
  &__moreConts {
    display: none;
  }

  &__imgFrame {
    width: 28rem;
    padding-right: 3rem;
  }
  // &__pic {
  //   padding-top: 64%;
  // }

  @include mq(sm) {
    &__item {
      padding: 1.5em;
    }
    &__contsFrame {
      padding-right: 0;
    }
    &__more {
      position: relative;
      margin-left: auto;
      transform: translateY(0);
    }
    &__moreConts {
      padding-top: 0.5em;
    }

    &__imgFrame {
      width: 22rem;
      padding-right: 1.2em;
    }
  }
  @include mq(xs) {
    &__item {
      flex-direction: column;
      padding: 1.2em;
    }

    &__imgFrame {
      width: 100%;
      margin-top: 1.5em;
      padding-right: 0;
    }
  }
}
