/*--------------------------------------------
  value
--------------------------------------------*/

// font-face
@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot?fsx2ee");
  src: url("../fonts/icomoon.eot?fsx2ee#iefix") format("embedded-opentype"),
    url("../fonts/icomoon.ttf?fsx2ee") format("truetype"), url("../fonts/icomoon.woff?fsx2ee") format("woff"),
    url("../fonts/icomoon.svg?fsx2ee#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

// media
$media_list: (
  "screen": "screen",
  "print": "print",
  "ss": "(max-width: 575.9px)",
  "ss-": "(min-width: 576px)",
  "ss-xs": "(min-width: 576px) and (max-width: 767.9px)",
  "xs": "(max-width: 767.9px)",
  "xs-": "(min-width: 768px)",
  "xs-sm": "(min-width: 768px) and (max-width: 991.9px)",
  "sm": "(max-width: 991.9px)",
  "sm-": "(min-width: 992px)",
  "sm-md": "(min-width: 992px) and (max-width: 1199.9px)",
  "md": "(max-width: 1199.9px)",
  "md-": "(min-width: 1200px)",
  "md-lg": "(min-width: 1200px) and (max-width: 1399.9px)",
  "lg": "(max-width: 1399.9px)",
  "lg-": "(min-width: 1400px)",
  "lg-xl": "(min-width: 1400px) and (max-width: 1599.9px)",
  "xl": "(max-width: 1599.9px)",
  "xl-": "(min-width: 1600px)",
);

// content gutter
$gutter: 5vw;

// color
$font: #333333;
$main: #c4ad63;
$sub: #a58a32;
$point: #31404a;
$link: #25b4e0;

$bd_l: #f4f4f4; // #f4f4f4
$bd: #c4c4c4; // #eeeeee
$bd_d: #aaaaaa; // #aaaaaa
$bd_input: #eeeeee; // #eeeeee

$white: #ffffff; // #ffffff
$black: #111111; // #111111
$black_l: #3f3b3a;

$gray_l: #f4f4f4; // #f4f4f4
$gray: #cccccc; // #cccccc
$gray_d: #777777; // #777777

$red: #cc1111; // #cc1111
$pink: #ff33bb; // #ff33bb
$purple: #8000ff; // #8000ff
$navy: #0000cc; // #0000cc
$blue: #0080ff; // #0080ff
$sky: #00bfe6; // #00bfe6
$green: #00e626; // #00e626
$yellow: #ffff00; // #ffff00
$orange: #ff8000; // #ff8000
$brown: #99331f; // #99331f
$beige: #f7f4ed;

// font
// $ff_base: "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "Hiragino Sans", "BIZ UDPGothic", "Yu Gothic Medium",
//   "Yu Gothic", Meiryo, Verdana, arial, sans-serif;
$ff_base: "Noto Serif JP", "Hiragino Mincho ProN", "Hiragino Mincho Pro", "BIZ UDPMincho", "Yu Mincho",
  "Times New Roman", Georgia, serif;
$ff_base_en: "Crimson Pro", "Noto Serif JP", "Hiragino Mincho ProN", "Hiragino Mincho Pro", "BIZ UDPMincho", "Yu Mincho",
  "Times New Roman", Georgia, serif;
$ff_option: "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "Hiragino Sans", "Yu Gothic Medium", "Yu Gothic",
  Meiryo, Verdana, arial, sans-serif;

$ff_sans: "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "Hiragino Sans", "BIZ UDPGothic", "Yu Gothic Medium",
  "Yu Gothic", Meiryo, Verdana, arial, sans-serif;
$ff_serif: "Hiragino Mincho ProN", "BIZ UDPMincho", "Yu Mincho", "Times New Roman", Georgia, serif;
$ff_en: "Roboto", "Open Sans", "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "Hiragino Sans", "BIZ UDPGothic",
  "Yu Gothic Medium", "Yu Gothic", Meiryo, Verdana, arial, sans-serif;
$ff_cursive: "HG行書体", cursive, serif;

// transition
$timing: cubic-bezier(0.15, 0.58, 0.15, 1);

// opacity
$opacity: 0.8;

// border-radius
$bdrs: 0.4em;
