/*--------------------------------------------
  COMMON
--------------------------------------------*/
.a-fade {
  &.js-anime {
    opacity: 0;

    &.is-anime {
      opacity: 1;
      transition: 0.8s 0.2s;
    }
  }
}

.a-alternateLayout {
  $amp: &;
  &.js-anime {
    > * {
      opacity: 0;

      &:last-child {
        transform: scale(1.1);
      }
    }

    &.-reverse {
      @include mq(sm-) {
        > * {
          &:first-child {
            transform: translateX(2em);
          }
        }
      }
    }

    @include mq(sm-) {
      > * {
        &:first-child {
          transform: translateX(-2em);
        }
      }
    }

    &.is-anime {
      > * {
        opacity: 1;
        transition: opacity 1.5s 0.4s, transform 1.5s 0.4s $timing;

        &:last-child {
          transform: scale(1);
        }
      }

      @include mq(sm-) {
        > * {
          &:first-child {
            transform: translateX(0em);
            transition-delay: 0.8s;
          }
        }
      }
      @include mq(sm) {
        > * {
          &:last-child {
            transition-delay: 0.8s;
          }
        }
      }
    }
  }
}

.a-secLayout {
  $amp: &;
  &.js-anime {
    &.u-bg_beige {
      opacity: 0;
    }
    &.c-deco_bgSide {
      &::before {
        opacity: 0;
        transform: translateX(5em);
      }

      &.-reverse {
        &::before {
          transform: translateX(-5em);
        }
      }
    }
    .p-obj_secLayout {
      &__titleFrame {
        opacity: 0;
        transform: translateY(5em);
      }
      &__contsFrame {
        opacity: 0;

        transform: translateX(-5em);

        &.-notMove {
          transform: translateX(0) !important;
        }
      }

      &.-reverse {
        .p-obj_secLayout__contsFrame {
          transform: translateX(5em);
        }
      }
    }
    .c-box_leftGap {
      opacity: 0;
    }

    &.is-anime {
      &.u-bg_beige {
        opacity: 1;
        transition: 0.8s 0.2s;
      }
      &.c-deco_bgSide {
        &::before {
          opacity: 1;
          transform: translateX(0) !important;
          transition: 0.8s 0.2s;
        }
      }
      .p-obj_secLayout {
        $secLayout: &;
        &__titleFrame,
        &__contsFrame {
          opacity: 1;
          transform: translate(0) !important;
          transition: 0.8s 0.6s;
        }
        &__contsFrame {
          transition-delay: 1s;
        }
      }
      .c-box_leftGap {
        opacity: 1;
        transition: 0.8s 1.8s;
      }
    }
  }
}

