/*--------------------------------------------
  label
--------------------------------------------*/
.c-label {
  display: inline-flex;
  justify-content: center;
  min-width: 12rem;
  padding: 0.5em 1em;
  background-color: $black_l;
  font-size: 1.2rem;
  color: $white;
  line-height: 1.3;

  @include mq(sm) {
    min-width: 10rem;
    padding: 0.4em 0.8em;
  }
}
