/*--------------------------------------------
  list
--------------------------------------------*/
.c-list {
  $amp: &;
  &__item {
    position: relative;
    padding-left: 1.6em;
    line-height: 1.5;

    &:not(:first-child) {
      margin-top: 0.4em;
    }
  }
  &__head {
    position: absolute;
    left: 0;
  }

  &.-pdl_sm {
    #{$amp}__item {
      padding-left: 1.2em;
    }
  }
  &.-pdl_lg {
    #{$amp}__item {
      padding-left: 3em;
    }
  }
}

/*--------------------------------------------
  bar gap
--------------------------------------------*/
.c-list_barGap {
  &__item {
    position: relative;

    & + & {
      margin-top: 3.75em;

      &::before {
        content: "";
        display: block;
        position: absolute;
        top: -2.7em;
        left: 1em;
        width: 1px;
        height: 1.2em;
        background-color: $bd;
      }
    }
  }
  &__title {
    position: relative;
    padding-left: 1.8em;
    font-weight: 500;
    font-size: 1.8rem;

    &::before {
      content: "";
      position: absolute;
      top: 0.55em;
      left: 0.55em;
      display: block;
      width: 0.75em;
      height: 0.75em;
      border-radius: 50%;
      background-color: $main;
    }

    .-en & {
      font-size: 2.2rem;
    }
  }
  &__conts {
    margin-top: 1.2em;
    line-height: 1.5;

    .-en & {
      margin-top: 0.6em;
      font-size: 2rem;
    }
  }

  @include mq(sm) {
    &__item {
      & + & {
        margin-top: 3em;

        &::before {
          top: -2.1em;
        }
      }
    }
    &__title {
      padding-left: 1.8em;
      font-size: 1.6rem;

      &::before {
        top: 0.6em;
        left: 0.6em;
        width: 0.6em;
        height: 0.6em;
      }

      .-en & {
        font-size: 2rem;
      }
    }
    &__conts {
      margin-top: 0.6em;

      .-en & {
        margin-top: 0.3em;
        font-size: 1.8rem;
      }
    }
  }
}

/*--------------------------------------------
  faq
--------------------------------------------*/
.c-list_faq {
  &__item {
    padding: 0 5em 3.75em 7.5em;
    border-bottom: 1px solid $bd;

    & + & {
      margin-top: 3.75em;
    }
  }

  &__title {
    position: relative;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.3;

    &::before {
      content: "Q.";
      position: absolute;
      display: block;
      transform: translateX(-5rem);
    }
  }
  &__conts {
    position: relative;
    margin-top: 1em;

    &::before {
      content: "A.";
      position: absolute;
      display: block;
      transform: translateX(-5rem);
    }
  }

  @include mq(sm) {
    &__item {
      padding: 0 3em 2em 5em;

      & + & {
        margin-top: 2em;
      }
    }

    &__title {
      font-size: 2rem;

      &::before {
        transform: translateX(-4rem);
      }
    }
    &__conts {
      margin-top: 0.8em;

      &::before {
        transform: translateX(-4rem);
      }
    }
  }
  @include mq(xs) {
    &__item {
      padding: 0 1em 2em 3em;
    }

    &__title {
      &::before {
        transform: translateX(-3rem);
      }
    }
    &__conts {
      &::before {
        transform: translateX(-3rem);
      }
    }
  }
}

/*--------------------------------------------
  dot
--------------------------------------------*/
.c-list_dot {
  $amp: &;
  &__item {
    position: relative;
    padding-left: 1.2em;

    & + & {
      margin-top: 0.3em;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0.6em;
      left: 0;
      width: 0.6em;
      height: 0.6em;
      border-radius: 50%;
      background-color: $black_l;
    }
  }

  &.-title {
    #{$amp}__item {
      & + * {
        margin-top: 2em;
      }

      &::before {
        font-size: 1.8rem;
      }
    }

    #{$amp}__title {
      margin-bottom: 0.4em;
      font-weight: 500;
      font-size: 1.8rem;
    }
  }

  @include mq(sm) {
    &__item {
      padding-left: 1.2em;
    }

    &.-title {
      #{$amp}__item {
        & + * {
          margin-top: 1.2em;
        }

        &::before {
          font-size: 1.6rem;
        }
      }

      #{$amp}__title {
        font-size: 1.6rem;
      }
    }
  }
}

/*--------------------------------------------
  flow
--------------------------------------------*/
.c-list_flow {
  $amp: &;
  border-top: 1px solid $bd;
  border-bottom: 1px solid $bd;

  &__item {
    display: flex;

    & + & {
      #{$amp}__conts {
        border-top: 1px solid $bd;
      }
    }
    &:last-child {
      #{$amp}__title {
        &::after {
          display: none;
        }
      }
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30rem;
    padding: 5rem 2rem 0;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.3;

    &::after {
      content: "";
      display: block;
      width: 1.5em;
      height: 1.5em;
      border-right: 2px solid $bd;
      border-bottom: 2px solid $bd;
      transform: rotate(45deg) skew(8deg, 8deg);
    }
  }
  &__conts {
    flex: 1;
    padding: 5rem 0;
  }

  @include mq(sm) {
    &__title {
      width: 20rem;
      padding: 3rem 1.5rem 0;
      font-size: 2rem;

      &::after {
        width: 1.2em;
        height: 1.2em;
      }
    }
    &__conts {
      padding: 3rem 0;
    }
  }
  @include mq(xs) {
    &__title {
      width: 15rem;
      padding: 3rem 1rem 0;
      font-size: 1.8rem;
    }
    &__conts {
      padding: 3rem 0;
    }
  }
}
