/*--------------------------------------------
  footer main
--------------------------------------------*/
.l-footer {
  background-color: $point;

  &__pageTop {
    z-index: 2;
    position: fixed;
    right: 5vw;
    bottom: 50px;
    opacity: 0;
    transition: opacity 0.4s;

    > a {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 6em;
      height: 6em;
      background-color: $point;
      border-radius: 50%;
      font-size: 1rem;
      color: $white;
      line-height: 1.1;

      &::before {
        content: "";
        display: block;
        width: 0.8em;
        height: 0.8em;
        margin-bottom: 0.8em;
        border-top: 1px solid $white;
        border-left: 1px solid $white;
        transform: rotate(45deg);
      }

      &:hover {
        opacity: $opacity;
      }
    }

    &.-white {
      > a {
        background-color: $white;
        color: $point;

        &::before {
          border-color: $point;
        }
      }
    }
    &.is-view {
      opacity: 1;
    }
  }

  @include mq(sm) {
    &__pageTop {
      right: 3vw;
      bottom: 15px;

      > a {
        width: 5em;
        height: 5em;

        &::before {
          width: 0.6em;
          height: 0.6em;
          margin-bottom: 0.5em;
        }
      }
    }
  }
}

.l-footer_conts {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__lang {
    margin-top: 1.8em;
    margin-bottom: 3.5em;

    .-en & {
      margin-top: 0;
    }
  }

  &__logo {
    display: flex;
    margin-top: 1.8em;

    > a {
      display: block;

      &:hover {
        opacity: $opacity;
      }
    }

    .-en & {
      margin-top: 0;
    }
  }

  &__address {
    margin-top: 1.8em;
    font-size: 1.4rem;

    .-en & {
      font-size: 1.6rem;
    }
  }

  &__copyright {
    margin-top: 3.5em;
    font-size: 1.4rem;

    .-en & {
      font-size: 1.6rem;
    }
  }

  @include mq(sm) {
    &__logo {
      margin-top: 1.5em;
    }

    &__address {
      margin-top: 1.5em;
      font-size: 1.2rem;

      .-en & {
        font-size: 1.4rem;
      }
    }

    &__copyright {
      margin-top: 2em;
      font-size: 1.2rem;

      .-en & {
        font-size: 1.4rem;
      }
    }
  }
}
