/*--------------------------------------------
  logo
--------------------------------------------*/
.c-logo {
  display: block;
  width: 22.5em;
  max-width: 100%;
  height: auto;

  _:-ms-lang(x)::-ms-backdrop,
  & {
    height: 3.75em;

    &.-vertical {
      height: 20em;
    }
  }

  &.-vertical {
    width: 6.5em;
  }

  &.-white {
    fill: $white;
  }

  &.-sdgs {
    width: 38em;

    _:-ms-lang(x)::-ms-backdrop,
    & {
      height: 4.7em;
    }
  }
  &.-sdgs_vertical {
    width: 16.25em;

    _:-ms-lang(x)::-ms-backdrop,
    & {
      height: 9em;
    }
  }
  &.-instagram {
    width: 16.25em;

    _:-ms-lang(x)::-ms-backdrop,
    & {
      height: 8em;
    }
  }
  &.-youtube {
    width: 10em;

    _:-ms-lang(x)::-ms-backdrop,
    & {
      height: 4em;
    }
  }

  @include mq(sm) {
    width: 18em;

    &.-vertical {
      width: 5.5em;
    }

    &.-sdgs {
      width: 30em;
    }
  }
}
